// ПРАВИЛА применения стилей для кнопок в секциях
// (SVG-шка внутри кнопки добавляется из спрайта через svg/use)

// ---
// HERO
// "btn btn-dark hero__btn hero__btn--dark" - темная кнопка
// "btn btn-light hero__btn hero__btn--light" - светлая кнопка

// ---
// CONTACTS
// "btn btn-dark contacts__btn contacts__btn--dark" - темная кнопка
// "btn btn-light contacts__btn contacts__btn--light" - светлая кнопка
//
// Cтиль для SVG-шки
// "contacts__btn-icon contacts__btn-icon--dark" - на темной кнопке
// "contacts__btn-icon contacts__btn-icon--light" - на светлой кнопке

// ---
// HEADER
// "btn header__btn"
//
// Cтиль для SVG-шки
// "header__btn-icon header__btn-icon--dark" - на темной кнопке
// "header__btn-icon header__btn-icon--light" - на светлой кнопке

// ---
// MOBILEMENU
// "btn menu__btn"
//
// Cтиль для SVG-шки
// use class="menu__btn-icon"
//

// Общие стили для кнопок
//
:root {
    --btn-color-light: #FFFFFF;
    --btn-color-dark: #D41443;
    --btn-color-light-accent: #ECEBEB;
    --btn-color-dark-accent: #C7133E;
}
    
.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    
    font-family: var(--font-base);
    font-style: normal;
    
    text-align: center;
    border-radius: 22px;
    border: none;
}
    
.btn--dark {
    color: var(--btn-color-light);
    background-color: var(--btn-color-dark);
    
    &:hover,
    &:focus {
    background-color: var(--btn-color-dark-accent);
}
    
.btn--light {
    color: var(--btn-color-dark);
    background-color: var(--btn-color-light);
    
    &:hover,
    &:focus {
    background-color: var(--btn-color-light-accent);
    }
}
    
    // Кнопки секции HERO
    
.hero__btn {
    height: 24px;
    
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;
    
    @include desk {
    height: 40px;
    
    font-size: 14px;
    line-height: 1.3;
    }
}
    
.hero__btn--dark {
    width: 62px;
    
    @include desk {
    width: 107px;
    }
}
    
.hero__btn--light {
    width: 83px;
    
    @include desk {
    width: 138px;
    }
}
    
    // Кнопки секции CONTACTS
    
.contacts__btn {
    height: 44px;
    
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;
}
    
.contacts__btn--dark {
    width: 161px;
    
    @include tab {
    width: 192px;
    }
    
    @include desk {
    width: 198px;
    }
}
    
.contacts__btn--light {
    width: 161px;
    
    @include tab {
    width: 174px;
    }
    
    @include desk {
    width: 180px;
    }
}
    
.contacts__btn-icon {
    width: 4px;
    height: 8px;
    
    fill: currentColor;
}
    
.contacts__btn-icon--dark {
    margin-left: 11px;
    
    @include tab {
    margin-left: 14px;
    }
}
    
.contacts__btn-icon--light {
    margin-left: 15px;
    
    @include tab {
    margin-left: 14px;
    }
}
    
    // Кнопки секции HEADER, MOBILEMENU
    
.header__btn,
.menu__btn {
     height: 44px;
    
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;
}
    
.header__btn {
    width: 160px;
}
    
.header__btn-icon {
    width: 4px;
    height: 8px;
    fill: currentColor;
}
    
.menu__btn {
    width: 209px;
    
    @include tab {
    width: 186px;
    }
}
    
.menu__btn-icon {
    margin-left: 16px;
    
    @include tab {
    margin-left: 14px;
    }
    
    fill: currentColor;
}
}
    

